import {Component, OnInit} from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {TimerProvider} from "../../providers/timer/timer";
import {NgForm} from "@angular/forms";
import {SimpleTimer} from "ng2-simple-timer";

/**
 * Generated class for the TimerPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-timer',
  templateUrl: 'timer.html',
  providers:[TimerProvider]
})
export class TimerPage implements OnInit{
  constructor(public navCtrl: NavController, public navParams: NavParams, public timerProvider:TimerProvider, private st: SimpleTimer) {}
  ngOnInit(){}
  ionViewDidLoad() {
    console.log('ionViewDidLoad TimerPage');
  }

  startTimer(){
    this.st.newTimer('5sec', 5);
  }

  onSubmit(form : NgForm) {
    console.log(form);
    console.log(this.timerProvider.prepareTime);
    this.startTimer();
  }
}



